import * as React from "react";
import './ScoresPage.css'
import Col from "react-bootstrap/Col";
import _ from "lodash";

import {Accordion, Card, Container, Row} from "react-bootstrap";
import {Team} from "../../services/TeamService";
import {TeamRoster} from "./TeamRoster";

export interface ScorePageState {
  teams: Array<Team>;
}
export class ScoresPage extends React.Component{

  state:ScorePageState;

  constructor() {
    super({});
    this.state = {teams:[]};
  }

  loadTeams(){
    // fetch(window.name +"/team/scoreView")
    fetch("http://20200103t024800-dot-bdsplayoffleague.appspot.com/team/scoreView")
    .then(response => response.json())
    .then(data => {
      this.setState({...this.state, teams: _.sortBy(data, 'score')})
    })
  }
  componentDidMount(): void {
    this.loadTeams();
  }



  render() {
    return (
      <Container>
        <Accordion >
          <Card>
            <Row className={"header"}>
              <Col lg={4}>Team Name</Col>
              <Col lg={2}>Players remaining</Col>
              <Col lg={2}>Score</Col>
              <Col lg={2}>Details</Col>
            </Row>
          </Card>
          {this.state.teams.map((team:Team)=>{
            return (<Card>
              <Accordion.Toggle as={Card.Header} eventKey={team.name}>
                <Row>
                <Col lg={4}>{team.name}</Col>
                <Col lg={2}>{team.playersRemaining}</Col>
                <Col lg={2}>{team.score}</Col>
                </Row>
              </Accordion.Toggle>
              <Accordion.Collapse  eventKey={team.name}>
                {/*<Card.Body>*/}
                  <TeamRoster
                    key={team.id}
                    players={team.players}/>
                {/*</Card.Body>*/}

              </Accordion.Collapse>
            </Card>);
          })}
        </Accordion>

      </Container>

    )
  }
}