import * as React from "react";
import {DraftPick} from "../services/DraftService";
import {FaTrashAlt, FaPencilAlt, FaPoop} from 'react-icons/fa';
import {Player} from "../services/RosterService";
import './DraftPage.css'

interface IState {
  pick: DraftPick;
  players: Array<Player>
  pickNumber: number;
  editPick: (num:number)=>void;
  getNeeds: (num:string)=>void;

}
export class DraftLine extends React.Component<IState> {

  state:IState;
  constructor(pickParam: IState) {

    super(pickParam);
    this.state = pickParam;
  }

  render() {
    return (
    <div className={"row"}>
      <div className={"col-1"}>{this.state.pick.draftPos}</div>
      <div className={"col-1"}>{this.state.pick.roundPick}</div>
      <div className={"col-4"}>{this.state.pick.teamName}</div>
      <div className={"col-4"}>
        {this.state.pick.playerName}
      </div>
      <div className={"col-1"}>
        {/*<FaPencilAlt onClick={()=>{this.state.editPick(this.state.pick.draftPos)}}/>*/}
        {/*<FaPoop onClick={()=>{this.state.getNeeds(this.state.pick.teamId)}}/>*/}
      </div>
    </div>);

  }
}